import React from 'react';
import {Swiper, SwiperSlide } from 'swiper/react';
import 'swiper/swiper-bundle.css';
import '../style.css'

const Slides = (props) => {

    const slideListRender = [];
    const slideList = props.data
    for(let i = 0; i < slideList.length; i++) {
        if(slideList[i].cat === props.collectionTitle) {
            slideListRender.push(
                <li key={`movie_${i}`}>
                    <div className="poster">
                        <div className="poster__info">
                            <div className="poster__textWrap">
                                <div className="poster__text">
                                    <h1 className="poster__name">{slideList[i].movie}</h1>
                                    <p className="poster__years">{slideList[i].date}</p>
                                </div>
                            </div>
                            <div className="poster__imgWrap">
                                <div className="poster__img">
                                    <div className="poster__imgBg">
                                    <img className="poster__img_img"
                                        src={slideList[i].img} 
                                        alt={`slide`} 
                                        onClick= {() => props.onClickListener(slideList[i])}/>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                    
                </li>
            )
        }
    }

    return (
    <div style={{listStyle: 'none'}} className="swipper-slide">
        <div className="collectionTitle">
            <h1 className="collectionTitle__name">{props.collectionTitle}</h1>
        </div>
        <div className="collectionDesc">
            <h3 className="collectionDesc_text">{props.collectionDesc}</h3>
        </div>

        <div className="slide_content">
            <ul>
                {slideListRender}
            </ul>
        </div>
        
    </div>);
}

export default Slides;